

import React, { useEffect, useRef } from 'react';
import L from 'leaflet';
import 'leaflet.markercluster';
import 'leaflet.markercluster/dist/MarkerCluster.css';
import 'leaflet.markercluster/dist/MarkerCluster.Default.css';
import 'leaflet.heat';
import { RefreshCw } from 'lucide-react';
import { AQIData } from '../../types/aqi';
import { getAQIColor, getAQILabel, getAQILevelInfo } from '../../utils/aqi';
import '../../types/leaflet-heat.d.ts';

// Mapping ta  sang tn khu vc H Ni
const hanoiDistricts: { [key: string]: string } = {
  '21.0333_105.8214': 'Ba nh',
  '21.0311_105.7924': 'Ba nh',
  '21.0285_105.8542': 'Hon Kim',
  '21.0278_105.8525': 'Hon Kim',
  '21.0075_105.8525': 'Hai B Trng',
  '21.0144_105.8678': 'Hai B Trng',
  '21.0167_105.8083': 'ng a',
  '21.0194_105.8089': 'ng a',
  '21.0758_105.8217': 'Ty H',
  '21.0833_105.8167': 'Ty H',
  '21.0333_105.7833': 'Cu Giy',
  '21.0378_105.7944': 'Cu Giy',
  '21.0167_105.7833': 'Thanh Xun',
  '20.9989_105.8092': 'Thanh Xun',
  '20.9742_105.8733': 'Hong Mai',
  '20.9833_105.8667': 'Hong Mai',
  '21.0458_105.8925': 'Long Bin',
  '21.0511_105.8864': 'Long Bin',
  '21.0139_105.7656': 'Nam T Lim',
  '21.0167_105.7500': 'Nam T Lim',
  '21.0667_105.7333': 'Bc T Lim',
  '21.0833_105.7500': 'Bc T Lim',
  '20.9717_105.7692': 'H ng',
  '20.9667_105.7667': 'H ng',
  '21.1333_105.5000': 'Sn Ty',
  '21.2500_105.4000': 'Ba V',
  '21.1167_105.4167': 'Phc Th',
  '21.0833_105.6167': 'an Phng',
  '21.0000_105.6833': 'Hoi c',
  '21.0333_105.6000': 'Quc Oai',
  '21.0167_105.5667': 'Thch Tht',
  '20.8667_105.7667': 'Chng M',
  '20.8500_105.8000': 'Thanh Oai',
  '20.8333_105.8833': 'Thng Tn',
  '20.7167_105.9000': 'Ph Xuyn',
  '20.7167_105.7667': 'ng Ha',
  '20.6833_105.8000': 'M c'
};

// Hm ly tn khu vc t ta 
const getDistrictName = (lat: number, lng: number): string => {
  const key = `${lat.toFixed(4)}_${lng.toFixed(4)}`;
  return hanoiDistricts[key] || `Khu vc ${lat.toFixed(3)}, ${lng.toFixed(3)}`;
};

interface MapProps {
  data: AQIData[];
  onLocationSelect: (location: AQIData) => void;
  selectedLocation: AQIData | null;
}

const Map = ({ data, onLocationSelect, selectedLocation }: MapProps) => {
  const mapRef = useRef<HTMLDivElement>(null);
  const mapInstanceRef = useRef<L.Map | null>(null);
  const markersRef = useRef<L.Marker[]>([]);
  const markerClusterGroupRef = useRef<L.MarkerClusterGroup | null>(null);
  const heatmapLayerRef = useRef<L.HeatLayer | null>(null);
  const markersLayerRef = useRef<L.LayerGroup | null>(null);
  const layerControlRef = useRef<L.Control.Layers | null>(null);

  useEffect(() => {
    if (!mapRef.current || mapInstanceRef.current) return;

    // Khi to bn 
    const map = L.map(mapRef.current).setView([21.0285, 105.8542], 10);
    mapInstanceRef.current = map;

    // Thm tile layer (OpenStreetMap)
    L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
      attribution: ' OpenStreetMap contributors'
    }).addTo(map);

    // Khi to MarkerClusterGroup vi cu hnh ty chnh
    const markerClusterGroup = (L as any).markerClusterGroup({
      spiderfyOnMaxZoom: true,
      showCoverageOnHover: false,
      zoomToBoundsOnClick: true,
      maxClusterRadius: 50,
      iconCreateFunction: function (cluster: any) {
        const childCount = cluster.getChildCount();
        let className = 'marker-cluster-small';

        if (childCount < 10) {
          className = 'marker-cluster-small';
        } else if (childCount < 20) {
          className = 'marker-cluster-medium';
        } else {
          className = 'marker-cluster-large';
        }

        return new L.DivIcon({
          html: '<div><span>' + childCount + '</span></div>',
          className: 'marker-cluster ' + className,
          iconSize: new L.Point(40, 40)
        });
      }
    });

    markerClusterGroupRef.current = markerClusterGroup;

    // To layer group cho markers
    const markersLayer = L.layerGroup();
    markersLayerRef.current = markersLayer;
    markersLayer.addLayer(markerClusterGroup);
    map.addLayer(markersLayer);

    // To layer control
    const baseMaps = {};
    const overlayMaps = {
      " im Quan Trc": markersLayer
    };

    const layerControl = L.control.layers(baseMaps, overlayMaps, {
      position: 'topright',
      collapsed: true
    });
    layerControlRef.current = layerControl;
    layerControl.addTo(map);

    // Thm click event  back v map ban u
    map.on('click', () => {
      console.log(' Map clicked - Back to main map view');
      // C th thm logic  reset view hoc focus vo map
    });

    // Thm AQI Legend hon chnh t frontend c
    const legend = (L as any).control({ position: 'bottomleft' });
    legend.onAdd = () => {
      const div = (L as any).DomUtil.create('div', 'aqi-legend');
      div.innerHTML = `
        <h3><i class="fas fa-palette"></i> Ch s AQI</h3>
        <div class="legend-items">
          <div class="legend-item" data-level="good">
            <span class="legend-emoji"></span>
            <div class="legend-color good"></div>
            <span>Tt (0-50 AQI)</span>
          </div>
          <div class="legend-item" data-level="moderate">
            <span class="legend-emoji"></span>
            <div class="legend-color moderate"></div>
            <span>Trung bnh (51-100 AQI)</span>
            
          </div>
          <div class="legend-item" data-level="unhealthy-sensitive">
            <span class="legend-emoji"></span>
            <div class="legend-color unhealthy-sensitive"></div>
            <span>Km (101-150 AQI)</span>
          </div>
          <div class="legend-item" data-level="unhealthy">
            <span class="legend-emoji"></span>
            <div class="legend-color unhealthy"></div>
            <span>Xu (151-200 AQI)</span>
          </div>
          <div class="legend-item" data-level="very-unhealthy">
            <span class="legend-emoji"></span>
            <div class="legend-color very-unhealthy"></div>
            <span>Rt xu (201-300 AQI)</span>
          </div>
          <div class="legend-item" data-level="hazardous">
            <span class="legend-emoji"></span>
            <div class="legend-color hazardous"></div>
            <span>Nguy hi (>300 AQI)</span>
          </div>
        </div>
      `;
      return div;
    };
    legend.addTo(map);

    // Cleanup function
    return () => {
      if (layerControlRef.current && mapInstanceRef.current) {
        mapInstanceRef.current.removeControl(layerControlRef.current);
        layerControlRef.current = null;
      }
      if (heatmapLayerRef.current && mapInstanceRef.current) {
        mapInstanceRef.current.removeLayer(heatmapLayerRef.current);
        heatmapLayerRef.current = null;
      }
      if (markersLayerRef.current && mapInstanceRef.current) {
        mapInstanceRef.current.removeLayer(markersLayerRef.current);
        markersLayerRef.current = null;
      }
      if (markerClusterGroupRef.current) {
        markerClusterGroupRef.current.clearLayers();
        markerClusterGroupRef.current = null;
      }
      if (mapInstanceRef.current) {
        mapInstanceRef.current.remove();
        mapInstanceRef.current = null;
      }
    };
  }, []);

  // To heatmap layer t d liu AQI
  const createHeatmapLayer = (data: AQIData[]) => {
    const heatmapData: [number, number, number][] = data.map(location => {
      const aqi = location.AQI_TOTAL || location.aqi || 0;
      // Normalize AQI value cho heatmap (0-1)
      const intensity = Math.min(aqi / 300, 1); // Max AQI ~300
      return [location.latitude, location.longitude, intensity];
    });

    // To custom gradient theo mu AQI
    const gradient = {
      0.0: '#00FF00', // Tt (Xanh l)
      0.17: '#FFFF00', // Trung bnh (Vng) 
      0.33: '#FF8C00', // Km (Cam)
      0.5: '#FF0000',  // Xu ()
      0.67: '#8B008B', // Rt xu (Tm)
      1.0: '#800000'   // Nguy hi ( m)
    };

    const heatLayer = (L as any).heatLayer(heatmapData, {
      radius: 25,
      blur: 20,
      maxZoom: 17,
      max: 1.0,
      minOpacity: 0.3,
      gradient: gradient
    });

    return heatLayer;
  };

  // Cp nht markers v heatmap khi data thay i
  useEffect(() => {
    if (!mapInstanceRef.current) return;

    console.log(' Map: Updating markers with data:', data);
    console.log(' Map: Data length:', data.length);
    console.log(' Map: First item:', data[0]);

    // Xa markers c khi cluster group
    if (markerClusterGroupRef.current) {
      markerClusterGroupRef.current.clearLayers();
    }
    markersRef.current = [];

    // Xa heatmap c
    if (heatmapLayerRef.current && layerControlRef.current) {
      layerControlRef.current.removeLayer(heatmapLayerRef.current);
      if (mapInstanceRef.current.hasLayer(heatmapLayerRef.current)) {
        mapInstanceRef.current.removeLayer(heatmapLayerRef.current);
      }
      heatmapLayerRef.current = null;
    }

    // To markers mi
    data.forEach((location, index) => {
      console.log(` Map: Processing location ${index}:`, location);
      console.log(` Map: AQI_TOTAL: ${location.AQI_TOTAL}, aqi: ${location.aqi}`);

      const aqi = location.AQI_TOTAL || location.aqi || 0;
      const color = getAQIColor(aqi);
      const label = getAQILabel(aqi);
      const levelInfo = getAQILevelInfo(aqi);

      console.log(` Map: Location ${index} - AQI: ${aqi}, Color: ${color}, Label: ${label}`);

      // To custom icon c cm theo mu AQI vi anchor points chnh xc
      const icon = L.divIcon({
        className: 'aqi-flag-marker',
        html: `
          <div class="flag-container" style="--flag-color: ${color};">
            <div class="flag-pole"></div>
            <div class="flag-body">
              <div class="flag-content">
                <div class="flag-aqi">${Math.round(aqi)}</div>
                <div class="flag-label">${label}</div>
              </div>
              <div class="flag-triangle"></div>
            </div>
            <div class="flag-shadow"></div>
          </div>
        `,
        iconSize: [60, 70],
        iconAnchor: [30, 65], // t anchor point  gia bottom ca flag
        popupAnchor: [0, -65]  // Tooltip s xut hin ngay pha trn flag
      });

      // To marker vi tooltip v thm vo cluster group
      const marker = L.marker([location.latitude, location.longitude], { icon });

      console.log(` Map: Created marker for location ${index} at [${location.latitude}, ${location.longitude}]`);

      // To tooltip content vi design hin i
      const tooltipContent = `
        <div class="aqi-tooltip">
          <div class="tooltip-header">
            <h3 class="tooltip-title">${getDistrictName(location.latitude, location.longitude)}</h3>
            <div class="tooltip-aqi">
              <span class="aqi-value" style="color: ${color};">${Math.round(aqi)}</span>
              <span class="aqi-status" style="color: ${color};">${label}</span>
            </div>
          </div>
          
          <div class="tooltip-metrics">
            <div class="metric-grid">
              <div class="metric-item">
                <div class="metric-left">
                  <span class="metric-icon"></span>
                  <span class="metric-label">PM2.5</span>
                </div>
                <span class="metric-value">${location.pm2_5?.toFixed(1) || 'N/A'} g/m</span>
              </div>
              <div class="metric-item">
                <div class="metric-left">
                  <span class="metric-icon"></span>
                  <span class="metric-label">PM10</span>
                </div>
                <span class="metric-value">${location.pm10?.toFixed(1) || 'N/A'} g/m</span>
              </div>
              <div class="metric-item">
                <div class="metric-left">
                  <span class="metric-icon"></span>
                  <span class="metric-label">Nhit </span>
                </div>
                <span class="metric-value">${location.temperature_2m?.toFixed(1) || 'N/A'}C</span>
              </div>
              <div class="metric-item">
                <div class="metric-left">
                  <span class="metric-icon"></span>
                  <span class="metric-label"> m</span>
                </div>
                <span class="metric-value">${location.relative_humidity_2m?.toFixed(1) || 'N/A'}%</span>
              </div>
              <div class="metric-item">
                <div class="metric-left">
                  <span class="metric-icon"></span>
                  <span class="metric-label">Gi</span>
                </div>
                <span class="metric-value">${location.wind_speed_10m?.toFixed(1) || 'N/A'} m/s</span>
              </div>
            </div>
          </div>
          
          <div class="tooltip-advice">
            <span class="advice-icon"></span>
            <span class="advice-text">${levelInfo.healthAdvice}</span>
          </div>
          
          <button 
            class="tooltip-button"
            onclick="window.openForecastDetail('${location.latitude}_${location.longitude}')"
          >
             Xem d bo chi tit
          </button>
        </div>
      `;

      // Bind tooltip vi cu hnh positioning chnh xc
      marker.bindTooltip(tooltipContent, {
        permanent: false,
        direction: 'top', // Lun hin th pha trn marker
        offset: [0, 0], // Khng offset thm, dng popupAnchor t icon
        className: 'custom-tooltip',
        opacity: 1,
        sticky: false, // Khng sticky  trnh jumping
        interactive: true
      });

      // X l click marker
      marker.on('click', () => {
        console.log(' Map: Marker clicked for location:', location);
        onLocationSelect(location);
      });

      // Thm marker vo cluster group thay v trc tip vo map
      if (markerClusterGroupRef.current) {
        markerClusterGroupRef.current.addLayer(marker);
      }

      markersRef.current.push(marker);
    });

    console.log(` Map: Created ${markersRef.current.length} markers`);

    // To heatmap layer mi
    if (data.length > 0) {
      const heatmap = createHeatmapLayer(data);
      heatmapLayerRef.current = heatmap;

      // Thm heatmap vo layer control
      if (layerControlRef.current) {
        layerControlRef.current.addOverlay(heatmap, " Bn  Nhit");
        console.log(' Map: Added heatmap layer to control');
      }
    }

    // Fit bounds nu c data
    if (data.length > 0 && markerClusterGroupRef.current) {
      // S dng cluster group  fit bounds
      const group = markerClusterGroupRef.current;
      if (group.getLayers().length > 0) {
        mapInstanceRef.current.fitBounds(group.getBounds(), { padding: [20, 20] });
        console.log(' Map: Fitted bounds to cluster data');
      }
    }
  }, [data, onLocationSelect]);

  // Highlight selected location
  useEffect(() => {
    if (!mapInstanceRef.current || !selectedLocation) return;

    // Xa highlight c
    markersRef.current.forEach(marker => {
      const icon = marker.getIcon() as L.DivIcon;
      const html = icon.options.html;
      if (typeof html === 'string' && html.includes('class="flag-container active"')) {
        const newIcon = L.divIcon({
          ...icon.options,
          html: html.replace('class="flag-container active"', 'class="flag-container"')
        });
        marker.setIcon(newIcon);
      }
    });

    // Highlight location c chn
    const selectedMarker = markersRef.current.find(marker => {
      const pos = marker.getLatLng();
      return Math.abs(pos.lat - selectedLocation.latitude) < 0.001 &&
        Math.abs(pos.lng - selectedLocation.longitude) < 0.001;
    });

    if (selectedMarker) {
      const icon = selectedMarker.getIcon() as L.DivIcon;
      const html = icon.options.html;
      if (typeof html === 'string') {
        const newIcon = L.divIcon({
          ...icon.options,
          html: html.replace('class="flag-container"', 'class="flag-container active"')
        });
        selectedMarker.setIcon(newIcon);
      }

      // Pan to selected location
      mapInstanceRef.current.panTo([selectedLocation.latitude, selectedLocation.longitude]);
    }
  }, [selectedLocation]);

  // Thm global functions  x l click t tooltip
  useEffect(() => {
    (window as any).selectLocation = (locationId: string) => {
      console.log(' Map: Global selectLocation called with:', locationId);
      const [lat, lng] = locationId.split('_');
      const location = data.find(loc =>
        loc.latitude.toFixed(3) === lat &&
        loc.longitude.toFixed(3) === lng
      );
      if (location) {
        console.log(' Map: Found location for popup click:', location);
        onLocationSelect(location);
      } else {
        console.log(' Map: Location not found for popup click');
      }
    };

    (window as any).openForecastDetail = (locationId: string) => {
      console.log(' Map: Opening forecast detail for:', locationId);
      const [lat, lng] = locationId.split('_');
      const location = data.find(loc =>
        Math.abs(loc.latitude - parseFloat(lat)) < 0.001 &&
        Math.abs(loc.longitude - parseFloat(lng)) < 0.001
      );

      if (location) {
        console.log(' Map: Found location for forecast:', location);
        // Trigger the location selection first
        onLocationSelect(location);

        // Then trigger the forecast page navigation
        // This will be handled by the parent App component
        const event = new CustomEvent('openForecast', {
          detail: { location }
        });
        window.dispatchEvent(event);
      } else {
        console.log(' Map: Location not found for forecast');
      }
    };

    return () => {
      delete (window as any).selectLocation;
      delete (window as any).openForecastDetail;
    };
  }, [data, onLocationSelect]);

  return (
    <div className="map-container">
      <div id="map" ref={mapRef}></div>

      <div className="map-controls">
        <button
          className="btn-refresh"
          onClick={() => window.location.reload()}
          title="Lm mi bn "
        >
          <RefreshCw size={16} />
          Lm mi
        </button>
      </div>
    </div>
  );
};

export default Map;
