import { AQILevel, AQILevelInfo } from '../types/aqi';

// AQI Calculation Functions - theo chun US EPA
export const calculatePM25AQI = (concentration: number): number => {
    if (concentration <= 0) return 0;

    // Breakpoints theo US EPA cho PM2.5
    const breakpoints = [
        { aqiLow: 0, aqiHigh: 50, cLow: 0.0, cHigh: 12.0 },
        { aqiLow: 51, aqiHigh: 100, cLow: 12.1, cHigh: 35.4 },
        { aqiLow: 101, aqiHigh: 150, cLow: 35.5, cHigh: 55.4 },
        { aqiLow: 151, aqiHigh: 200, cLow: 55.5, cHigh: 150.4 },
        { aqiLow: 201, aqiHigh: 300, cLow: 150.5, cHigh: 250.4 },
        { aqiLow: 301, aqiHigh: 500, cLow: 250.5, cHigh: 500.4 }
    ];

    for (const bp of breakpoints) {
        if (concentration >= bp.cLow && concentration <= bp.cHigh) {
            // p dng cng thc ni suy tuyn tnh
            const aqi = ((bp.aqiHigh - bp.aqiLow) / (bp.cHigh - bp.cLow)) *
                (concentration - bp.cLow) + bp.aqiLow;
            return Math.round(aqi);
        }
    }

    return 500; // Nu vt qu, tr v max AQI
};

export const getAQILevel = (aqi: number): AQILevel => {
    if (aqi <= 50) return 'good';
    if (aqi <= 100) return 'moderate';
    if (aqi <= 150) return 'unhealthy-sensitive';
    if (aqi <= 200) return 'unhealthy';
    if (aqi <= 300) return 'very-unhealthy';
    return 'hazardous';
};

// Cp nht thang mu AQI theo chun mi t hnh nh
export const getAQIColor = (aqi: number): string => {
    if (aqi <= 50) return '#00E400';      // Xanh l cy - Tt
    if (aqi <= 100) return '#FFFF00';     // Vng - Trung bnh  
    if (aqi <= 150) return '#FF7E00';     // Cam - Km
    if (aqi <= 200) return '#FF0000';     //  - Xu
    if (aqi <= 300) return '#8F3F97';     // Tm - Rt xu
    return '#7E0023';                     // Nu - Nguy hi
};

export const getAQILabel = (aqi: number): string => {
    if (aqi <= 50) return 'Tt';
    if (aqi <= 100) return 'Trung bnh';
    if (aqi <= 150) return 'Km';
    if (aqi <= 200) return 'Xu';
    if (aqi <= 300) return 'Rt xu';
    return 'Nguy hi';
};

export const getAQITips = (aqi: number): string => {
    if (aqi <= 50) return 'Khng nh hng n sc khe. Thch hp cho mi hot ng ngoi tri.';
    if (aqi <= 100) return 'Ngi nhy cm c th b nh hng nh. Ph hp cho hu ht mi ngi.';
    if (aqi <= 150) return 'Ngi nhy cm nn hn ch ra ngoi. Mi ngi nn theo di sc khe.';
    if (aqi <= 200) return 'Tt c mi ngi bt u b nh hng n sc khe. Hn ch hot ng ngoi tri.';
    if (aqi <= 300) return 'nh hng nghim trng n sc khe. Mi ngi nn trnh hot ng ngoi tri.';
    return 'Bo ng khn cp v sc khe. Khuyn ngh  trong nh v ng ca s.';
};

export const getAQILevelInfo = (aqi: number): AQILevelInfo => {
    const level = getAQILevel(aqi);
    const label = getAQILabel(aqi);
    const color = getAQIColor(aqi);
    const tips = getAQITips(aqi);

    const emojis = {
        'good': '',
        'moderate': '',
        'unhealthy-sensitive': '',
        'unhealthy': '',
        'very-unhealthy': '',
        'hazardous': ''
    };

    const descriptions = {
        'good': 'Cht lng khng kh tt',
        'moderate': 'Cht lng khng kh trung bnh',
        'unhealthy-sensitive': 'Cht lng khng kh km',
        'unhealthy': 'Cht lng khng kh xu',
        'very-unhealthy': 'Cht lng khng kh rt xu',
        'hazardous': 'Cht lng khng kh nguy hi'
    };

    return {
        level,
        label,
        color,
        emoji: emojis[level],
        description: descriptions[level],
        healthAdvice: tips
    };
};

